import * as GW from 'gw-utils';
import * as CONST from './gw';
import { Hall } from './room';
const DIRS = GW.utils.DIRS;
export function pickHallWidth(opts = {}) {
    return GW.utils.clamp(_pickHallWidth(opts), 1, 3);
}
function _pickHallWidth(opts) {
    if (typeof opts === 'number')
        return opts;
    if (!opts)
        return 1;
    if (opts.hallWidth === undefined)
        return 1;
    let width = opts.hallWidth;
    if (typeof width === 'number')
        return width;
    else if (Array.isArray(width)) {
        // @ts-ignore
        width = GW.random.weighted(width) + 1;
    }
    else {
        width = GW.random.weighted(width);
    }
    if (typeof width === 'string')
        return Number.parseInt(width);
    return width;
}
export function pickHallLength(dir, opts) {
    const horizontalLength = GW.utils.firstOpt('horizontalHallLength', opts, [
        9,
        15,
    ]);
    const verticalLength = GW.utils.firstOpt('verticalHallLength', opts, [
        2,
        9,
    ]);
    if (dir == GW.utils.UP || dir == GW.utils.DOWN) {
        return verticalLength;
    }
    else {
        return horizontalLength;
    }
}
export function pickHallDirection(grid, room, opts) {
    const doors = room.doors;
    // Pick a direction.
    let dir = opts.dir || GW.utils.NO_DIRECTION;
    if (dir == GW.utils.NO_DIRECTION) {
        const dirs = GW.random.sequence(4);
        for (let i = 0; i < 4; i++) {
            dir = dirs[i];
            const length = pickHallLength(dir, opts)[1]; // biggest measurement
            const dx = doors[dir][0] + Math.floor(DIRS[dir][0] * length);
            const dy = doors[dir][1] + Math.floor(DIRS[dir][1] * length);
            if (doors[dir][0] != -1 &&
                doors[dir][1] != -1 &&
                grid.hasXY(dx, dy)) {
                break; // That's our direction!
            }
            else {
                dir = GW.utils.NO_DIRECTION;
            }
        }
    }
    return dir;
}
export function pickHallExits(grid, x, y, dir, opts) {
    let newX, newY;
    const obliqueChance = GW.utils.firstOpt('obliqueChance', opts, 15);
    const allowObliqueHallwayExit = GW.random.chance(obliqueChance);
    const hallDoors = [
        [-1, -1],
        [-1, -1],
        [-1, -1],
        [-1, -1],
    ];
    for (let dir2 = 0; dir2 < 4; dir2++) {
        newX = x + DIRS[dir2][0];
        newY = y + DIRS[dir2][1];
        if ((dir2 != dir && !allowObliqueHallwayExit) ||
            !grid.hasXY(newX, newY) ||
            grid[newX][newY]) {
            hallDoors[dir2][0] = -1;
            hallDoors[dir2][1] = -1;
        }
        else {
            hallDoors[dir2][0] = newX;
            hallDoors[dir2][1] = newY;
        }
    }
    return hallDoors;
}
export function digHall(grid, dir, length, room, opts) {
    const door = room.doors[dir];
    const DIR = DIRS[dir];
    let x = door[0];
    let y = door[1];
    const tile = opts.tile || CONST.FLOOR;
    for (let i = 0; i < length; i++) {
        grid[x][y] = tile;
        x += DIR[0];
        y += DIR[1];
    }
    x -= DIR[0];
    y -= DIR[1];
    const hall = new Hall(door, dir, length);
    hall.doors = pickHallExits(grid, x, y, dir, opts);
    return hall;
}
export function digHallTwo(grid, dir, length, room, opts) {
    const door = room.doors[dir];
    const tile = opts.tile || CONST.FLOOR;
    const hallDoors = [
        [-1, -1],
        [-1, -1],
        [-1, -1],
        [-1, -1],
    ];
    let x0, y0;
    let hall;
    if (dir === GW.utils.UP) {
        x0 = Math.max(door[0] - 1, room.x);
        y0 = door[1] - length + 1;
        for (let x = x0; x < x0 + 2; ++x) {
            for (let y = y0; y < y0 + length; ++y) {
                grid[x][y] = tile;
            }
        }
        hallDoors[dir] = [x0, y0 - 1];
        hall = new Hall([x0, door[1]], dir, length, 2);
    }
    else if (dir === GW.utils.DOWN) {
        x0 = Math.max(door[0] - 1, room.x);
        y0 = door[1] + length - 1;
        for (let x = x0; x < x0 + 2; ++x) {
            for (let y = y0; y > y0 - length; --y) {
                grid[x][y] = tile;
            }
        }
        hallDoors[dir] = [x0, y0 + 1];
        hall = new Hall([x0, door[1]], dir, length, 2);
    }
    else if (dir === GW.utils.LEFT) {
        x0 = door[0] - length + 1;
        y0 = Math.max(door[1] - 1, room.y);
        for (let x = x0; x < x0 + length; ++x) {
            for (let y = y0; y < y0 + 2; ++y) {
                grid[x][y] = tile;
            }
        }
        hallDoors[dir] = [x0 - 1, y0];
        hall = new Hall([door[0], y0], dir, length, 2);
    }
    else {
        //if (dir === GW.utils.RIGHT) {
        x0 = door[0] + length - 1;
        y0 = Math.max(door[1] - 1, room.y);
        for (let x = x0; x > x0 - length; --x) {
            for (let y = y0; y < y0 + 2; ++y) {
                grid[x][y] = tile;
            }
        }
        hallDoors[dir] = [x0 + 1, y0];
        hall = new Hall([door[0], y0], dir, length, 2);
    }
    hall.doors = hallDoors;
    hall.width = 2;
    return hall;
}
export function attachHallway(grid, room, opts) {
    opts = opts || {};
    const dir = pickHallDirection(grid, room, opts);
    if (dir === GW.utils.NO_DIRECTION)
        return null;
    console.log('dir', dir);
    const length = GW.random.range(...pickHallLength(dir, opts));
    console.log('length', length);
    const width = opts.width || 1;
    console.log('width', width);
    if (width > 1) {
        return digHallTwo(grid, dir, length, room, opts);
    }
    return digHall(grid, dir, length, room, opts);
}
